:root {
  --main-color: #000;
  --link-active-color: #00acdc;
  --white-background-color: #fff;
}

body {
  color: var(--main-color);
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 100%;
}

.wrapper {
  flex-direction: column;
  min-height: 100%;
  display: flex;
  overflow: hidden;
}

.header__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
}

.header__body {
  background-color: var(--white-background-color);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  flex-shrink: 0;
  display: flex;
}

.header__img {
  vertical-align: top;
  width: 100%;
  max-width: 100%;
  max-height: 40px;
}

.menu__list {
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu__item:not(:last-child) {
  margin-right: 20px;
}

.menu__link {
  text-transform: uppercase;
  cursor: pointer;
  color: inherit;
  justify-content: center;
  align-items: center;
  height: 60px;
  line-height: 15px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.menu__link:hover, .is-active {
  color: var(--link-active-color);
}

.is-active:after {
  content: "";
  background-color: var(--link-active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
